<template>
  <v-card class="OrderTable">
    <v-data-table
      disable-pagination
      hide-default-footer
      :items="items"
      :headers="headers"
      dense
    >
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title>الطلبات</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <div>
            <v-text-field
              prepend-inner-icon="mdi-magnify"
              label="بحث"
              v-model="search"
            ></v-text-field>
          </div>
          <v-spacer></v-spacer>
        </v-toolbar>
      </template>
      <template v-slot:[`item.origin`]="{ item }">
        {{ getGeo(item.origin) }}
      </template>
      <template v-slot:[`item.destination`]="{ item }">
        {{ getGeo(item.destination) }}
      </template>
      <template v-slot:[`item.status`]="{ item }">
        {{ getStatus(item.status) }}
      </template>
      <template v-slot:[`item.tripDetails`]="{ item }">
        {{ getTripDetails(item.tripDetails) }}
      </template>
      <template v-slot:[`item.show`]="{ item }">
        <v-icon color="info" @click="showBooking(item)">mdi-eye</v-icon>
      </template>
    </v-data-table>
  </v-card>
</template>
<script>
import Headers from "../constants/Headers";
export default {
  data() {
    return {
      headers: Headers.orderHeader,
      search: "",
    };
  },
  props: ["items"],
  methods: {
    getGeo(item) {
      var _item = JSON.parse(item);
      let geo = _item.geo;
      return geo;
    },
    getStatus(status) {
      let text = "";
      if (status == 0) {
        text = "في الأنتظار";
      } else if (status == 1) {
        text = "في أنتظار السائق";
      } else if (status == 2) {
        text = "بانتظار الزبون";
      } else if (status == 3) {
        text = "داخل الرحلة";
      } else if (status == 4) {
        text = "تقيم";
      } else if (status == 5) {
        text = "طلب مكتمل";
      }

      return text;
    },
    getTripDetails(tripDetails) {
      var _tripDetails = JSON.parse(tripDetails);
      var fare = parseFloat(_tripDetails.fare);
      return (
        "وقت الرحلة:" +
        _tripDetails.duration.text +
        " " +
        "مسافة الرحلة:" +
        _tripDetails.distance.text +
        " المبلغ المحصل: " +
        fare.toFixed(2)
      );
    },
    showBooking(item) {
      this.$router.push({ name: "ShowOrder", params: { id: item.ID } });
    },
  },
};
</script>
