<template>
  <div class="BlockSwitch">
    <v-switch
      v-model="internalBlock"
      :label="internalBlock == 0 ? 'Active' : 'Blocked'"
      @change="changeValue"
    />
  </div>
</template>
<script>
import axios from "axios";
import { env } from "../../../constants";
export default {
  props: ["userID", "block"],
  data() {
    return {
      internalUserID: 0,
      internalBlock: 0,
    };
  },
  methods: {
    install() {
      let vm = this;
      vm.internalUserID = vm.userID;
      vm.internalBlock = vm.block == 0 ? false : true;
    },
    changeValue(value) {
      console.log(value);
      this.internalBlock = value;
      let block = 0;
      if (value === true) {
        block = 1;
      }
      console.log(block);

      axios
        .post(env.server + "driversDashboard/toggleUserBlock", {
          userID: this.internalUserID,
          block: block,
        })
        .then((res) => {
          console.log(res);
          alert("Update success");
        })
        .catch((err) => {
          console.log(err);
          alert("Update error");
        });
    },
  },
  mounted() {
    this.install();
  },
};
</script>
