<template>
  <v-container fluid>
    <v-dialog v-model="itemDialog" max-width="50%">
      <v-card>
        <v-toolbar flat color="primary" dense>
          <v-toolbar-title>{{ newItemTitle }}</v-toolbar-title>
        </v-toolbar>
        <v-container fill-height>
          <v-text-field
            label="العنوان"
            v-model="item.title"
            dense
            outlined
          ></v-text-field>

          <v-text-field
            label="اللون"
            v-model="item.color"
            dense
            outlined
          ></v-text-field>

          <v-textarea
            label="المحتوى"
            v-model="item.content"
            dense
            rows="2"
            outlined
          ></v-textarea>
          <v-row justify="center" v-if="inputType == 'new'">
            <v-col cols="4">
              <v-card style="cursor: pointer">
                <v-img
                  @click="openImageSelect"
                  contain
                  :src="
                    image == '' ? require('../../assets/image_icon.png') : image
                  "
                  width="150"
                  height="150"
                ></v-img>
              </v-card>
            </v-col>
          </v-row>
          <input
            v-if="inputType == 'new'"
            type="file"
            class="hideImageInput"
            ref="imageInput"
            @change="selectFile"
            prepend-icon="mdi-camera"
            label="اضافة صورة"
          />
          <v-row justify="center" v-if="inputType == 'edit'">
            <v-col cols="4">
              <v-card style="cursor: pointer">
                <v-img
                  @click="openImageSelect"
                  contain
                  :src="
                    item.image == ''
                      ? require('../../assets/image_icon.png')
                      : serverURI + 'public/' + scope + '/' + item.image
                  "
                  width="150"
                  height="150"
                ></v-img>
              </v-card>
            </v-col>
          </v-row>
          <input
            v-if="inputType == 'edit'"
            type="file"
            class="hideImageInput"
            ref="imageInput"
            @change="selectFile"
            prepend-icon="mdi-camera"
            label="صورة جديدة"
          />
        </v-container>
        <v-card-actions>
          <v-row>
            <v-col cols="6"
              ><v-btn
                color="success"
                block
                @click="inputType == 'new' ? store() : update()"
                >حفظ</v-btn
              ></v-col
            >
            <v-col cols="6"
              ><v-btn color="info" block @click="itemDialog = false"
                >الغاء</v-btn
              ></v-col
            >
          </v-row>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-data-table
      disable-pagination
      hide-default-footer
      :items="items"
      :headers="headers"
      dense
    >
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title>السلايدات</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <div>
            <v-text-field
              prepend-inner-icon="mdi-magnify"
              label="بحث"
              v-model="search"
            ></v-text-field>
          </div>
          <v-spacer></v-spacer>
          <v-btn color="primary" dark @click="openNew">
            {{ newItemTitle }}
          </v-btn>
        </v-toolbar>
      </template>
      <template v-slot:[`item.edit`]="{ item }">
        <v-icon small color="blue" class="mr-2" @click="openEdit(item)">
          mdi-pencil
        </v-icon>
        <v-icon @click="openDestroy(item)" small color="red">
          mdi-delete
        </v-icon>
      </template>
      <template v-slot:[`item.image`]="{ item }">
        <v-img
          :src="serverURI + 'public/' + scope + '/' + item.image"
          contain
          width="100"
          height="100"
        ></v-img>
      </template>
    </v-data-table>
  </v-container>
</template>
<script>
import axios from "axios";
import { env, Headers } from "../../constants";
export default {
  data() {
    return {
      newItemTitle: "اضافة سلايد جديد",
      item: {},
      items: [],
      itemDialog: false,
      scope: "appIntro",
      headers: Headers.appIntro,
      search: "",
      inputType: "new",
      serverURI: env.server,
      image: "",
      load: false,
      services: [],
      subServices: [],
    };
  },
  methods: {
    install() {
      let vm = this;
      axios
        .get(env.server + vm.scope + "/index")
        .then((res) => {
          let items = [];
          res.data.forEach((trg) => {
            let service = {};
            if (trg.serviceID == 10000) {
              service = { title: "الرئيسية", ID: 10000 };
            } else if (trg.serviceID == 10001) {
              service = { title: "صفحة التسجيل", ID: 10001 };
            } else {
              service = trg.service;
            }

            items.push({ ...trg, service: service });
          });
          vm.items = items;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    store() {
      let vm = this;

      // ---- Marshel Fixed ---- //

      // ---- End Marshel Fixed ---- //
      axios
        .post(env.server + vm.scope + "/store", vm.item)
        .then((res) => {
          let trg = res.data;
          let service = {};
          if (trg.serviceID == 10000) {
            service = { title: "الرئيسية", ID: 10000 };
          } else if (trg.serviceID == 10001) {
            service = { title: "صفحة التسجيل", ID: 10001 };
          } else {
            service = trg.service;
          }
          vm.items.push({ ...trg, service: service });
          vm.itemDialog = false;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    update() {
      let vm = this;
      axios
        .post(env.server + vm.scope + "/update", vm.item)
        .then((res) => {
          vm.items = res.data.ads;
          vm.itemDialog = false;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    destroy() {
      let vm = this;
      axios
        .get(env.server + vm.scope + "/destroy/" + vm.item.ID)
        .then((res) => {
          vm.items = res.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    openNew() {
      this.item = {};
      this.inputType = "new";
      this.itemDialog = true;
    },
    openEdit(item) {
      let vm = this;
      vm.item = item;
      vm.inputType = "edit";
      vm.itemDialog = true;
    },
    openDestroy(item) {
      let vm = this;
      vm.item = item;
      confirm("هل تريد الحذف ؟") && vm.destroy();
    },
    selectFile(event) {
      let vm = this;
      vm.load = true;
      const data = new FormData();
      data.append("image", event.target.files[0]);
      axios
        .post(env.server + "basics/upload_image/" + vm.scope, data)
        .then((res) => {
          vm.load = false;
          vm.item.image = res.data.image;
          vm.image = vm.serverURI + "public/" + vm.scope + "/" + res.data.image;
        })
        .catch((err) => {
          console.log(err);
          vm.load = false;
          vm.$store.commit("StoreToast", {
            text: "يوجد خطا غير معروف الرجاء المحاولة مرة أخرى",
            color: "red",
          });
        });
    },
    changeService() {
      let vm = this;
      axios
        .get(env.server + "subServices/indexWithServiceID/" + vm.item.serviceID)
        .then((res) => {
          vm.subServices = res.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    openImageSelect() {
      let vm = this;
      vm.$refs.imageInput.click();
    },
  },
  mounted() {
    this.install();
  },
};
</script>
