<template>
  <v-container fluid>
    <v-data-table
      disable-pagination
      hide-default-footer
      :items="items"
      :headers="headers"
      dense
    >
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title>كافة العملاء</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <div>
            <v-text-field
              prepend-inner-icon="mdi-magnify"
              label="بحث"
              v-model="search"
            ></v-text-field>
          </div>
          <v-spacer></v-spacer>
        </v-toolbar>
      </template>
      <template v-slot:[`item.block`]="{ item }">
        <v-switch
          :input-value="item.status !== 3"
          @change="toggleStatus(item)"
          :label="item.status !== 3 ? 'Active' : 'Blocked'"
          color="success"
        ></v-switch>
      </template>
      <template v-slot:[`item.show`]="{ item }">
        <v-icon small color="blue" class="mr-2" @click="showUser(item)">
          mdi-eye
        </v-icon>
      </template>
    </v-data-table>
  </v-container>
</template>
<script>
import axios from "axios";
import { env, Headers } from "../../constants";
export default {
  data() {
    return {
      newItemTitle: "اضافة عامل جديد",
      item: {
        user: {
          name: "",
          phone: "",
          password: "",
        },
        serviceID: 0,
      },
      items: [],
      itemDialog: false,
      scope: "orders",
      headers: Headers.allClients,
      search: "",
      inputType: "new",
      serverURI: env.server,
      image: "",
      load: false,
      services: [],
    };
  },
  methods: {
    install() {
      let vm = this;
      axios
        .get(env.server + "dashboard/indexAllClients")
        .then((res) => {
          vm.items = res.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    openOrder(order) {
      this.$router.push({ name: "ShowOrder", params: { id: order.ID } });
    },
    openNew() {
      this.item = {
        user: {
          name: "",
          phone: "",
          password: "",
        },
        serviceID: 0,
      };
      this.inputType = "new";
      this.itemDialog = true;
    },
    openEdit(item) {
      let vm = this;
      vm.item = item;
      vm.inputType = "edit";
      vm.itemDialog = true;
    },
    openDestroy(item) {
      let vm = this;
      vm.item = item;
      confirm("هل تريد الحذف ؟") && vm.destroy();
    },
    store() {
      let vm = this;

      // ---- Marshel Fixed ---- //
      vm.item.user.status = 2;
      vm.item.user.user_type = 2;

      // ---- End Marshel Fixed ---- //
      axios
        .post(env.server + "suppliers/storeSupplierUser", vm.item)
        .then(() => {
          window.location.reload();
        })
        .catch((err) => {
          console.log(err);
        });
    },
    showUser(item) {
      this.$router.push({ name: "ShowUser", params: { id: item.ID } });
    },
    toggleStatus(item) {
      axios
        .get(env.server + "users/toggleStatus/" + item.ID)
        .then((res) => {
          console.log(res.data);
        })
        .catch((err) => {
          console.log(err.response);
        });
    },
  },
  mounted() {
    this.install();
  },
};
</script>
