<template>
  <div class="SupplierMaps" style="height: 100%">
    <div class="filterBoard" style="max-width: 500px; padding-bottom: 2px">
      <v-row>
        <v-col col="4">
          <h3>فلترة النتائج</h3>
        </v-col>
        <v-col col="4">
          <v-select
            label="التصنيفات"
            v-model="categoryID"
            :items="categories"
            item-text="title"
            item-value="ID"
            dense
            outlined
          ></v-select>
        </v-col>
        <v-col col="4">
          <v-btn color="primary" @click="indexSuppliersInfoWithServiceID" block
            >بحث</v-btn
          >
        </v-col>
      </v-row>
    </div>
    <gmap-map :zoom="8" :center="center" style="width: 100%; height: 84vh">
      <gmap-marker
        :key="index"
        v-for="(m, index) in locationMarkers"
        :position="{
          lat: m.supplierInfo.latitude,
          lng: m.supplierInfo.longitude,
        }"
        @click="
          supplierInfo = {
            ID: m.ID,
            name: m.name,
            phone: m.phone,
            service: m.supplierInfo.service.title,
            category: m.supplierInfo.category.title,
            status: m.status,
          }
        "
      ></gmap-marker>
    </gmap-map>
    <div class="whiteBoard">
      <div class="board">
        <h3 style="text-align: center">المعلومات</h3>
        <p>حدد العامل لمعلوماته</p>
        <v-divider />
        <v-list>
          <v-list-item>
            <v-list-item-content>SupplierID:</v-list-item-content>
            <v-list-item-content class="align-end">
              {{ supplierInfo.ID }}
            </v-list-item-content>
          </v-list-item>
          <v-list-item>
            <v-list-item-content>Name:</v-list-item-content>
            <v-list-item-content class="align-end">
              {{ supplierInfo.name }}
            </v-list-item-content>
          </v-list-item>
          <v-list-item>
            <v-list-item-content>Phone:</v-list-item-content>
            <v-list-item-content class="align-end">
              {{ supplierInfo.phone }}
            </v-list-item-content>
          </v-list-item>
          <v-list-item>
            <v-list-item-content>Category:</v-list-item-content>
            <v-list-item-content class="align-end">
              {{ supplierInfo.category }}
            </v-list-item-content>
          </v-list-item>
          <v-list-item>
            <v-list-item-content>Status:</v-list-item-content>
            <v-list-item-content class="align-end">
              <span style="color: blue" v-if="supplierInfo.status == 1"
                >غير مفعل</span
              >
              <span style="color: green" v-if="supplierInfo.status == 2"
                >مفعل</span
              >
              <span style="color: red" v-if="supplierInfo.status == 3"
                >ملغي</span
              >
            </v-list-item-content>
          </v-list-item>
        </v-list>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import { env } from "../../constants";
export default {
  name: "AddGoogleMap",
  data() {
    return {
      center: {
        lat: 31.945368,
        lng: 35.928371,
      },
      locationMarkers: [],
      existingPlace: null,
      supplierInfo: {
        ID: 0,
        name: 0,
        phone: 0,
        service: "",
        category: "",
        status: 100,
      },
      services: [],
      serviceID: 0,
      categories: [],
      categoryID: 0,
    };
  },

  mounted() {
    this.locateGeoLocation();
    this.addLocationMarker();
    this.installServices();
  },

  methods: {
    initMarker(loc) {
      this.existingPlace = loc;
    },
    installServices() {
      let vm = this;
      axios
        .get(env.server + "categories/index")
        .then((res) => {
          vm.categories = res.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    indexSuppliersInfoWithServiceID() {
      let vm = this;
      let categoryID = vm.categoryID;
      if (vm.categoryID == 0) {
        alert("الرجاء اختيار خدمة");
        return;
      }
      axios
        .get(
          env.server + "suppliers/indexSupplierInfoWithServiceID/" + categoryID
        )
        .then((res) => {
          vm.locationMarkers = res.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    addLocationMarker() {
      axios
        .get(env.server + "suppliers/indexSupplierInfo")
        .then((res) => {
          let data = res.data;
          this.locationMarkers = data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    locateGeoLocation: function () {
      navigator.geolocation.getCurrentPosition((res) => {
        this.center = {
          lat: res.coords.latitude,
          lng: res.coords.longitude,
        };
      });
    },
  },
};
</script>
<style>
.whiteBoard {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 30%;
}
.filterBoard {
  position: relative;
  background-color: white;
}
.board {
  padding: 15px;
  border-radius: 10px;
  background-color: white;
}
</style>
