<template>
  <div class="ShowDriver">
    <div v-if="show">
      <list-detail
        title="معلومات المستخدم"
        :values="[
          { icon: 'account', title: 'الأسم', value: driver.name, type: 'text' },
          {
            icon: 'phone',
            title: 'رقم الهاتف',
            value: driver.phone,
            type: 'text',
          },
          {
            icon: 'eye',
            title: 'حالة الحساب',
            value: driver.block == 0 ? 'فعال' : 'محظور',
            type: 'text',
          },
          {
            icon: 'eye',
            title: 'حالة السائق',
            value: getValueStatus(driver.driverDetails.status),
            type: 'text',
          },
        ]"
      />
      <br />
      <v-card style="padding: 10px" v-if="driver.registerStatus == 101">
        <v-subheader>معلومات الموقع</v-subheader>
        <maps-button :driverDetail="driver.driverDetails" />
      </v-card>
      <br />
      <list-detail
        title="معلومات السيارة"
        :values="[
          {
            icon: 'car',
            title: 'الشركة',
            value: driver.driverCar.carBrand,
            type: 'text',
          },
          {
            icon: 'car',
            title: 'الموديل',
            value: driver.driverCar.carModel,
            type: 'text',
          },
          {
            icon: 'car',
            title: 'سنة الصنع ',
            value: driver.driverCar.carYear,
            type: 'text',
          },
          {
            icon: 'car',
            title: 'نمرة السيارة',
            value: driver.driverCar.carPlate,
            type: 'text',
          },
        ]"
      />
      <br />
      <div v-if="driver.registerStatus == 100">
        <list-detail title="المعلومات المقدمة" :values="driverValues" />
      </div>
      <list-detail
        v-if="driver.registerStatus == 101"
        title="معلومات الرصيد"
        :values="[
          {
            icon: 'wallet',
            title: 'رصيد المحفظة',
            value: driver.wallet.balance,
            type: 'text',
          },
        ]"
      />
      <br />
      <div v-if="driver.registerStatus == 101">
        <list-detail
          title="الحركات على الرصيد"
          :values="walletLogs"
          v-if="walletLogs.length !== 0"
        />
        <v-card v-if="walletLogs.length == 0">
          <v-subheader>الحركات على الرصيد</v-subheader>
          <v-card align="center" justify="center">
            <p style="text-align: center">لا يوجد حركات</p>
          </v-card>
        </v-card>
      </div>

      <br />
      <div v-if="driver.registerStatus == 101">
        <order-table :items="bookings" />
      </div>
    </div>
    <div v-if="driver.registerStatus == 100">
      <v-btn
        color="success"
        style="margin-left: 10px; margin-right: 10px"
        @click="approveDriverRegister"
        >الموافقة على الطلب</v-btn
      >
      <v-btn color="error" @click="cancelDriverRegister">الغاء الطلب</v-btn>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import ListDetail from "../../components/ListDetail.vue";
import { env } from "../../constants";
import MapsButton from "./layouts/MapsButton.vue";
import OrderTable from "../../components/OrderTable.vue";
export default {
  components: { ListDetail, MapsButton, OrderTable },
  data() {
    return {
      driver: {},
      show: false,
      walletLogs: [],
      bookings: [],
      driverValues: [],
    };
  },
  mounted() {
    let vm = this;
    axios
      .get(env.server + "driversDashboard/showDriver/" + vm.$route.params.id)
      .then((res) => {
        let user = res.data.user;
        let bookings = res.data.bookings;

        if (res.data.user.driverValues) {
          res.data.user.driverValues.forEach((trg) => {
            // increase , decrease
            vm.driverValues.push({
              icon: "wallet",
              title: trg.key,
              value: trg.value,
              type: trg.type,
            });
          });
        }
        vm.driver = user;
        vm.show = true;
        vm.driver.walletLogs.forEach((trg) => {
          // increase , decrease
          vm.walletLogs.push({
            icon: "wallet",
            title: trg.type == "increase" ? "اضافة رصيد" : "خصم رصيد",
            value: trg.balance,
            type: "text",
          });
        });

        vm.bookings = bookings;
      })
      .catch((err) => {
        alert("There are error");
        console.log(err.response);
      });
  },
  methods: {
    getValueStatus(status) {
      let text = "";
      if (status == 0) {
        text = "خارج الخدمة";
      } else if (status == 1) {
        text = "خارج الخدمة";
      } else if (status == 2) {
        text = "متوفر";
      } else if (status == 3) {
        text = "مشغول";
      }
      return text;
    },
    approveDriverRegister() {
      let vm = this;
      axios
        .get(
          env.server + "driversDashboard/approveDriverRegister/" + vm.driver.ID
        )
        .then((res) => {
          console.log(res);
          alert("تم تاكيد العملية");
          window.location.reload();
        })
        .catch((err) => {
          console.log(err);
          alert("يوجد خطا الرجاء المحاولة مرة اخرى");
        });
    },
    cancelDriverRegister() {
      let vm = this;
      axios
        .get(
          env.server + "driversDashboard/cancelDriverRegister/" + vm.driver.ID
        )
        .then((res) => {
          console.log(res);
          alert("تم تاكيد العملية");
          window.location.reload();
        })
        .catch((err) => {
          console.log(err);
          alert("يوجد خطا الرجاء المحاولة مرة اخرى");
        });
    },
  },
};
</script>
