<template>
  <v-container fluid>
    <v-data-table
      disable-pagination
      hide-default-footer
      :items="items"
      :headers="headers"
      dense
    >
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title>Block List</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <div>
            <v-text-field
              prepend-inner-icon="mdi-magnify"
              label="بحث"
              v-model="search"
            ></v-text-field>
          </div>
          <v-spacer></v-spacer>
        </v-toolbar>
      </template>
      <template v-slot:[`item.edit`]="{ item }">
        <v-icon small color="green" class="mr-2" @click="approveSupplier(item)">
          mdi-check
        </v-icon>
      </template>
      <template v-slot:[`item.status`]="{ item }">
        <span v-if="item.status == 3" style="color: red"> Blocked </span>
        <span v-if="item.status == 2" style="color: green"> Actuve </span>
        <span v-if="item.status == 1" style="color: blue"> On Hold </span>
      </template>
    </v-data-table>
  </v-container>
</template>
<script>
import axios from "axios";
import { env, Headers } from "../../constants";
export default {
  data() {
    return {
      newItemTitle: "اضافة خدمة جديدة",
      item: {},
      items: [],
      itemDialog: false,
      scope: "orders",
      headers: Headers.supplierRequest,
      search: "",
      inputType: "new",
      serverURI: env.server,
      image: "",
      load: false,
    };
  },
  methods: {
    install() {
      let vm = this;
      axios
        .get(env.server + "suppliers/IndexBlockListSupplier")
        .then((res) => {
          vm.items = res.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    approveSupplier(user) {
      let vm = this;
      confirm("هل تريد تفعيل الحساب والموافقة عليه ؟") &&
        axios
          .get(env.server + "suppliers/approve/" + user.ID)
          .then((res) => {
            alert("Approve Done");
            vm.install();
            console.log(res);
          })
          .catch((err) => {
            console.log(err);
          });
    },
    blockSupplier(user) {
      let vm = this;
      confirm("هل تريد الغاء الحساب ؟") &&
        axios
          .get(env.server + "suppliers/block/" + user.ID)
          .then((res) => {
            alert("Block Done");
            vm.install();
            console.log(res);
          })
          .catch((err) => {
            console.log(err);
          });
    },
    openOrder(order) {
      this.$router.push({ name: "ShowOrder", params: { id: order.ID } });
    },
  },
  mounted() {
    this.install();
  },
};
</script>
