<template>
  <div class="ShowOrder" v-if="show">
    <list-detail
      title="معلومات الزبون"
      :values="[
        {
          icon: 'account',
          title: 'الأسم',
          value: booking.rider.name,
          type: 'text',
        },
        {
          icon: 'phone',
          title: 'رقم الهاتف',
          value: booking.rider.phone,
          type: 'text',
        },
      ]"
    />
    <list-detail
      title="معلومات السائق"
      :values="[
        {
          icon: 'account',
          title: 'الأسم',
          value: booking.driver.name,
          type: 'text',
        },
        {
          icon: 'phone',
          title: 'رقم الهاتف',
          value: booking.driver.phone,
          type: 'text',
        },
      ]"
    />
    <list-detail
      title="معلومات الطلب"
      :values="[
        {
          icon: 'account',
          title: 'رقم الطلب',
          value: booking.ID,
          type: 'text',
        },
        {
          icon: 'phone',
          title: 'تاريخ الطلب',
          value: booking.CreatedAt,
          type: 'text',
        },
        {
          icon: 'phone',
          title: 'حالة الطلب',
          value: getStatus(booking.status),
          type: 'text',
        },
        {
          icon: 'phone',
          title: 'نوع الطلب',
          value: booking.bookingType,
          type: 'text',
        },
      ]"
    />
    <list-detail
      title="معلومات الرحلة"
      :values="[
        {
          icon: 'account',
          title: 'من',
          value: getGeo(booking.origin),
          type: 'text',
        },
        {
          icon: 'phone',
          title: 'الى',
          value: getGeo(booking.destination),
          type: 'text',
        },
        {
          icon: 'phone',
          title: 'معلومات',
          value: getTripDetails(booking.tripDetails),
          type: 'text',
        },
      ]"
    />
  </div>
</template>
<script>
import axios from "axios";
import { env } from "../../../constants";
import ListDetail from "../../../components/ListDetail.vue";
export default {
  components: { ListDetail },
  data() {
    return {
      booking: {},
      show: false,
    };
  },
  methods: {
    install() {
      let vm = this;
      let id = vm.$route.params.id;

      axios
        .get(env.server + "bookingDashboard/showBooking/" + id)
        .then((res) => {
          console.log(res.data);
          vm.booking = res.data;
          vm.show = true;
        })
        .catch((err) => {
          console.log(err);
          alert("يوجد خطا الرجاء المحاولة مرة أخرى");
        });
    },
    getStatus(status) {
      let text = "";
      if (status == 0) {
        text = "في الأنتظار";
      } else if (status == 1) {
        text = "في أنتظار السائق";
      } else if (status == 2) {
        text = "بانتظار الزبون";
      } else if (status == 3) {
        text = "داخل الرحلة";
      } else if (status == 4) {
        text = "تقيم";
      } else if (status == 5) {
        text = "طلب مكتمل";
      }
      return text;
    },
    getGeo(detail) {
      let _details = JSON.parse(detail);
      console.log(_details);
      let geo = _details.geo;

      return geo;
    },
    getTripDetails(tripDetails) {
      var _tripDetails = JSON.parse(tripDetails);
      var fare = parseFloat(_tripDetails.fare);
      return (
        "وقت الرحلة:" +
        _tripDetails.duration.text +
        " - " +
        "مسافة الرحلة:" +
        _tripDetails.distance.text +
        " - " +
        " المبلغ المحصل: " +
        fare.toFixed(2)
      );
    },
  },
  mounted() {
    this.install();
  },
};
</script>
