<template>
  <div class="ShowSupplier" v-if="show">
    <v-card tile>
      <v-list disabled>
        <v-subheader>المستخدم</v-subheader>
        <v-list-item-group color="primary">
          <v-list-item>
            <v-list-item-icon>
              <v-icon>mdi-account</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>{{ user.name }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-list-item>
            <v-list-item-icon>
              <v-icon>mdi-phone</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>{{ user.phone }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-card>
    <v-card>
      <order-table :items="orders" />
    </v-card>
  </div>
</template>
<script>
import { env, Headers } from "../../constants";
import axios from "axios";
import OrderTable from "../../components/OrderTable.vue";
export default {
  components: { OrderTable },
  data() {
    return {
      user: {
        userInfo: {
          country: {
            title: "",
          },
          city: {
            title: "",
          },
        },
      },
      orders: [],
      headers: Headers.orders,
      show: false,
    };
  },
  methods: {
    install() {
      let vm = this;
      let id = vm.$route.params.id;

      axios
        .get(env.server + "dashboard/showUser/" + id)
        .then((res) => {
          vm.user = res.data.user;
          vm.orders = res.data.orders;
          vm.show = true;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    openOrder(order) {
      this.$router.push({ name: "ShowOrder", params: { id: order.ID } });
    },
  },
  mounted() {
    let vm = this;
    vm.install();
  },
};
</script>
