<template>
  <div class="ShowSupplier">
    <v-card tile>
      <v-list disabled>
        <v-subheader>العامل</v-subheader>
        <v-list-item-group color="primary">
          <v-list-item>
            <v-list-item-icon>
              <v-icon>mdi-account</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>{{ supplier.name }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-list-item>
            <v-list-item-icon>
              <v-icon>mdi-phone</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>{{ supplier.phone }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-list-item>
            <v-list-item-icon>
              <v-icon>mdi-note</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>{{
                supplier.supplierInfo.category.title
              }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-list-item>
            <v-list-item-icon>
              <v-icon>mdi-note</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-img
                width="125"
                height="125"
                style="max-width: 125px !important"
                :src="
                  supplier.avatar == ''
                    ? require('../../assets/avatar.png')
                    : serverUri + 'public/users/' + supplier.avatar
                "
              ></v-img>
            </v-list-item-content>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-card>
    <v-card>
      <v-data-table
        disable-pagination
        hide-default-footer
        :items="orders"
        :headers="headers"
        dense
      >
        <template v-slot:top>
          <v-toolbar flat>
            <v-toolbar-title>فائمة الطلبات</v-toolbar-title>
            <v-divider class="mx-4" inset vertical></v-divider>
            <div>
              <v-text-field
                prepend-inner-icon="mdi-magnify"
                label="بحث"
                v-model="search"
              ></v-text-field>
            </div>
            <v-spacer></v-spacer>
          </v-toolbar>
        </template>
        <template v-slot:[`item.edit`]="{ item }">
          <v-icon small color="blue" class="mr-2" @click="openOrder(item)">
            mdi-eye
          </v-icon>
        </template>
        <template v-slot:[`item.serviceOptions`]="{ item }">
          <span v-for="(i, index) in item.serviceOptions" :key="index"
            >[{{ i.title }}]</span
          >
        </template>
      </v-data-table>
    </v-card>
  </div>
</template>
<script>
import { env, Headers } from "../../constants";
import axios from "axios";
export default {
  data() {
    return {
      supplier: {},
      orders: [],
      headers: Headers.orders,
      serverUri: env.server,
    };
  },
  methods: {
    install() {
      let vm = this;
      let id = vm.$route.params.id;

      axios
        .get(env.server + "suppliers/showSupplier/" + id)
        .then((res) => {
          vm.supplier = res.data.supplier;
          vm.orders = res.data.orders;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    openOrder(order) {
      this.$router.push({ name: "ShowOrder", params: { id: order.ID } });
    },
  },
  mounted() {
    let vm = this;
    vm.install();
  },
};
</script>
