<template>
  <div id="app">
    <v-app
      v-if="routeName == 'Auth'"
      style="font-family: 'El Messiri', sans-serif !important"
    >
      <v-main>
        <router-view></router-view>
        <v-snackbar v-model="toast.show" :timeout="3000" :color="toast.color">
          {{ toast.text }}
        </v-snackbar>
      </v-main>
    </v-app>
    <v-app v-else style="font-family: 'El Messiri', sans-serif !important">
      <v-app-bar
        class="print-hide"
        dense
        app
        color="primary"
        dark
        elevation="0"
      >
        <v-spacer />
      </v-app-bar>
      <v-navigation-drawer
        dark
        permanent
        app
        right
        color="primary"
        class="print-hide"
      >
        <v-list>
          <v-list-item class="px-2">
            <v-list-item-avatar>
              <v-img :src="require('./assets/avatar.png')"></v-img>
            </v-list-item-avatar>
          </v-list-item>

          <v-list-item link>
            <v-list-item-content>
              <v-list-item-title class="title">
                {{ user.name }}
              </v-list-item-title>
              <v-list-item-subtitle>{{ user.phone }}</v-list-item-subtitle>
            </v-list-item-content>
          </v-list-item>
        </v-list>

        <v-divider></v-divider>
        <v-list nav dense>
          <v-list-group
            v-for="item in pages"
            :key="item.scope"
            :prepend-icon="item.icon"
            no-action
            active-class="black--text"
            :style="{
              paddingBottom: item.hasDiv ? '10px' : '0px',
              marginBottom: item.hasDiv ? '10px' : '0px',
              borderBottom: item.hasDiv
                ? 'solid rgba(255,255,255,0.5) 0.5px'
                : 'solid black 0px',
            }"
          >
            <template v-slot:activator>
              <v-list-item-content>
                <v-list-item-title v-text="item.title"></v-list-item-title>
              </v-list-item-content>
            </template>
            <div style="background-color: black">
              <v-list-item
                link
                v-for="child in item.subPages"
                :key="child.scope"
                @click="navigate(child.scope)"
              >
                <v-list-item-content>
                  <v-list-item-title v-text="child.title"></v-list-item-title>
                  <div
                    v-if="child.end"
                    style="border-bottom: 1px solid white; margin-top: 10px"
                  ></div>
                </v-list-item-content>
              </v-list-item>
            </div>
          </v-list-group>
        </v-list>
      </v-navigation-drawer>
      <v-main>
        <v-container fluid>
          <v-row class="fill-height">
            <v-col>
              <transition name="fade">
                <router-view></router-view>
              </transition>
            </v-col>
          </v-row>
          <v-snackbar v-model="toast.show" :timeout="3000" :color="toast.color">
            {{ toast.text }}
          </v-snackbar>
        </v-container>
      </v-main>
      <v-footer></v-footer>
    </v-app>
  </div>
</template>
<script>
import axios from "axios";
import { env, Pages } from "./constants";

export default {
  name: "App",
  data: () => ({
    sidebarMenu: false,
    pages: [],
    type: "Main",
  }),
  methods: {
    navigate(scope) {
      let vm = this;
      vm.$router.push(scope);
    },
    checkUser() {
      let vm = this;
      if (window.localStorage.getItem("userToken")) {
        let token = window.localStorage.getItem("userToken");

        axios
          .get(env.server + "auth/auth", {
            headers: {
              Authorization: "Bearer " + token,
            },
          })
          .then((res) => {
            let user = res.data.user;
            window.localStorage.setItem("userToken", token);
            vm.$store.commit("StoreUser", user);
            if (vm.routeName == "Auth") {
              vm.$router.push("Home");
            }
          })
          .catch((err) => {
            console.log(err);
            if (vm.routeName !== "Auth") {
              vm.$router.push("/");
            }
          });
      } else {
        if (vm.routeName !== "Auth") {
          vm.$router.push("/");
        }
      }
    },
  },
  computed: {
    routeName() {
      return this.$route.name;
    },
    toast() {
      return this.$store.state.global.toast;
    },
    user() {
      return this.$store.state.auth.user;
    },
  },
  mounted() {
    this.checkUser();
    this.pages = Pages;
  },
};
</script>
<style>
::-webkit-scrollbar {
  display: none;
}
body {
  font-family: "El Messiri", sans-serif !important;
}
.hideImageInput {
  position: fixed;
  left: -999999px;
}
</style>
