<template>
  <div class="MapsButton" style="padding-bottom: 3px; padding-top: 3px">
    <v-btn small color="info" outlined @click="openMap">Open map</v-btn>
  </div>
</template>
<script>
export default {
  props: ["driverDetail"],
  methods: {
    openMap() {
      let vm = this;
      //   https://www.google.com/maps/search/?api=1&query=47.5951518%2C-122.3316393
      window.open(
        `https://www.google.com/maps/search/?api=1&query=${vm.driverDetail.latitude}%2C${vm.driverDetail.longitude}`
      );
    },
  },
};
</script>
