<template>
  <v-container fluid>
    <order-table :items="items" />
  </v-container>
</template>
<script>
import axios from "axios";
import OrderTable from "../../components/OrderTable.vue";
import { env } from "../../constants";

export default {
  components: { OrderTable },
  data() {
    return {
      items: [],
    };
  },
  methods: {
    install() {
      let vm = this;
      axios
        .get(env.server + "bookingDashboard/IndexBooking/waitingRiders")
        .then((res) => {
          if (res.data) {
            vm.items = res.data;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  mounted() {
    this.install();
  },
};
</script>
