<template>
  <div class="Auth">
    <v-container fluid fill-height>
      <v-layout align-center justify-center>
        <v-flex xs12 sm8 md4>
          <v-card class="elevation-12">
            <v-toolbar dark color="primary">
              <v-toolbar-title>تسجيل دخول</v-toolbar-title>
            </v-toolbar>
            <v-card-text>
              <v-form v-model="vform" ref="form">
                <v-text-field
                  prepend-icon="mdi-account"
                  label="رقم الهاتف"
                  type="text"
                  :rules="rules.nameRules"
                  v-model="user.phone"
                  v-on:keyup.enter="login"
                ></v-text-field>
                <v-text-field
                  id="password"
                  prepend-icon="mdi-lock"
                  label="رمز المرور"
                  type="password"
                  :rules="rules.passwordRules"
                  v-model="user.password"
                  v-on:keyup.enter="login"
                ></v-text-field>
              </v-form>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="primary" @click="login">تسجيل دخول</v-btn>
            </v-card-actions>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>
<script>
import axios from "axios";
import { env } from "../../constants";
export default {
  data() {
    return {
      user: {
        phone: "",
        password: "",
      },
      rules: {
        nameRules: [(v) => !!v || "رقم الهاتف مطلوب"],
        passwordRules: [(v) => !!v || "رمز المرور مطلوب"],
      },
      vform: false,
    };
  },
  name: "Auth",
  methods: {
    login() {
      let vm = this;
      vm.$refs.form.validate();
      if (vm.vform) {
        axios
          .post(env.server + "auth/login", vm.user)
          .then((res) => {
            let user = res.data.user;
            let token = res.data.token;

            window.localStorage.setItem("userToken", token);
            vm.$store.commit("StoreUser", user);

            vm.$router.push("home");
          })
          .catch((err) => {
            if (err.response.data.message == "record not found") {
              vm.$store.commit("StoreToast", {
                text: "لا يوجد مستخدم بهذا الرقم",
                color: "red",
              });
            } else if (
              err.response.data.message ==
              "crypto/bcrypt: hashedPassword is not the hash of the given password"
            ) {
              vm.$store.commit("StoreToast", {
                text: "يوجد خطا في رقم الهاتف او رمز المرور",
                color: "red",
              });
            } else {
              vm.$store.commit("StoreToast", {
                text: "يوجد خطأ غير معروف",
                color: "red",
              });
            }
          });
      }
    },
  },
  
};
</script>