let Pos = {
    state: {
        posItems:[],
        billItems:[],
        sell:{
          take:''
        }
      },
      mutations: {
        storePosItems(state,payload) {
          state.posItems = payload
        },
        pushBillItem(state,payload) {
          let _billItems = state.billItems
          let has = false
          _billItems.forEach((billItem,index) => {
            if(billItem.item.ID == payload.item.ID) {
              has = true;
              _billItems[index].qty = billItem.qty + 1
            }
          })
          if(!has) {
            state.billItems = [..._billItems,payload]
          }else {
            state.billItems = _billItems
          }
        },
        changeQty(state,payload) {
          let itemID = payload.itemID
          let type = payload.type
          let _billItems = state.billItems
          _billItems.forEach((item,index) => {
            if(item.item.ID == itemID) {
              if(type == "+") {
                _billItems[index].qty = item.qty + 1
              }else {
                if(item.qty !== 1) {
                  _billItems[index].qty = item.qty - 1
                }
              }
            }
          })
          state.billItems = _billItems
        },
        removePosItem(state,payload) {
          let itemID = payload.itemID
          let _billItems = state.billItems
          _billItems.forEach((billItem,index) => {
            if(billItem.item.ID == itemID) {
              _billItems.splice(index,1)
            }
          })
          state.billItems = _billItems
        },
        changeSellTake(state,payload) {
          let type = payload.type
          let number = payload.number
          if(type == "add") {
            if(number == ".") {
              if(state.sell.take == "") {
                state.sell.take = "0."
              }else {
                if(!state.sell.take.includes(".")) {
                  state.sell.take = state.sell.take + "."
                }
              }
            }else {
              state.sell.take = state.sell.take + "" + number
            }
          }
          if(type == "backspace") {
            if(state.sell.take !== null &&state.sell.take.length > 0) {
              state.sell.take = state.sell.take.substring(0,state.sell.take.length - 1)
            }
          }
        },
        resetBill(state) {
          state.billItems = []
        }
      },
      actions: {
       
      }
     
}

export default Pos;