<template>
  <v-container fluid>
    <v-dialog v-model="itemDialog" max-width="50%">
      <v-card>
        <v-toolbar flat color="primary" dense>
          <v-toolbar-title>{{ newItemTitle }}</v-toolbar-title>
        </v-toolbar>
        <v-container fill-height>
          <v-text-field
            label="اسم الدولة"
            v-model="item.title"
            dense
            outlined
          ></v-text-field>
          <v-text-field
            label="كود الدولة"
            v-model="item.code"
            dense
            outlined
          ></v-text-field>
          <v-text-field
            label="العملة"
            v-model="item.currency"
            dense
            outlined
          ></v-text-field>
          <v-text-field
            label="اللغة"
            v-model="item.language"
            dense
            outlined
          ></v-text-field>
          <v-text-field
            label="كود الهاتف"
            v-model="item.phoneCode"
            dense
            outlined
          ></v-text-field>
        </v-container>
        <v-card-actions>
          <v-row>
            <v-col cols="6"
              ><v-btn
                color="success"
                block
                @click="inputType == 'new' ? store() : update()"
                >حفظ</v-btn
              ></v-col
            >
            <v-col cols="6"
              ><v-btn color="info" block @click="itemDialog = false"
                >الغاء</v-btn
              ></v-col
            >
          </v-row>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-data-table
      disable-pagination
      hide-default-footer
      :items="items"
      :headers="headers"
      dense
    >
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title>الدول</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <div>
            <v-text-field
              prepend-inner-icon="mdi-magnify"
              label="بحث"
              v-model="search"
            ></v-text-field>
          </div>
          <v-spacer></v-spacer>
          <v-btn color="primary" dark @click="openNew">
            {{ newItemTitle }}
          </v-btn>
        </v-toolbar>
      </template>
      <template v-slot:[`item.edit`]="{ item }">
        <v-icon small color="blue" class="mr-2" @click="openEdit(item)">
          mdi-pencil
        </v-icon>
        <v-icon @click="openDestroy(item)" small color="red">
          mdi-delete
        </v-icon>
      </template>
    </v-data-table>
  </v-container>
</template>
<script>
import axios from "axios";
import { env, Headers } from "../../constants";
export default {
  data() {
    return {
      newItemTitle: "اضافة دولة جديدة",
      item: {},
      items: [],
      itemDialog: false,
      scope: "countries",
      headers: Headers.countriesHeaders,
      search: "",
      inputType: "new",
    };
  },
  methods: {
    install() {
      let vm = this;
      axios
        .get(env.server + vm.scope + "/index")
        .then((res) => {
          vm.items = res.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    store() {
      let vm = this;

      // ---- End Marshel Fixed ---- //
      axios
        .post(env.server + vm.scope + "/store", vm.item)
        .then((res) => {
          vm.items.push(res.data);
          vm.itemDialog = false;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    update() {
      let vm = this;
      axios
        .post(env.server + vm.scope + "/update", vm.item)
        .then((res) => {
          vm.items = res.data.countries;
          vm.itemDialog = false;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    destroy() {
      let vm = this;
      axios
        .get(env.server + vm.scope + "/destroy/" + vm.item.ID)
        .then((res) => {
          vm.items = res.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    openNew() {
      this.item = {};
      this.inputType = "new";
      this.itemDialog = true;
    },
    openEdit(item) {
      let vm = this;
      vm.item = item;
      vm.inputType = "edit";
      vm.itemDialog = true;
    },
    openDestroy(item) {
      let vm = this;
      vm.item = item;
      confirm("هل تريد الحذف ؟") && vm.destroy();
    },
  },
  mounted() {
    this.install();
  },
};
</script>
