<template>
  <v-container fluid>
    <v-dialog v-model="itemDialog" max-width="50%">
      <v-card>
        <v-toolbar flat color="primary" dense>
          <v-toolbar-title>{{ newItemTitle }}</v-toolbar-title>
        </v-toolbar>
        <v-container fill-height>
          <v-row justify="center" v-if="inputType == 'new'">
            <v-col cols="4">
              <v-card style="cursor: pointer" width="150">
                <v-img
                  round
                  align-center
                  @click="openImageSelect"
                  contain
                  :src="
                    image == '' ? require('../../assets/image_icon.png') : image
                  "
                  width="150"
                  height="150"
                ></v-img>
                <p>الصورة الشخصية</p>
              </v-card>
            </v-col>
            <v-col cols="4">
              <v-card style="cursor: pointer" width="150">
                <v-img
                  round
                  align-center
                  @click="openImageSelect"
                  contain
                  :src="
                    image == '' ? require('../../assets/image_icon.png') : image
                  "
                  width="150"
                  height="150"
                ></v-img>
                <p>صورة فحص كورونا</p>
              </v-card>
            </v-col>
            <v-col cols="4">
              <v-card style="cursor: pointer" width="150">
                <v-img
                  round
                  align-center
                  @click="openImageSelect"
                  contain
                  :src="
                    image == '' ? require('../../assets/image_icon.png') : image
                  "
                  width="150"
                  height="150"
                ></v-img>
                <p>صورة الهوية الشخصية</p>
              </v-card>
            </v-col>
          </v-row>
          <input
            v-if="inputType == 'new'"
            type="file"
            class="hideImageInput"
            ref="imageInput"
            @change="selectFile"
            prepend-icon="mdi-camera"
            label="اضافة صورة"
          />
          <v-row justify="center" v-if="inputType == 'edit'">
            <v-col cols="4">
              <v-card style="cursor: pointer" width="150">
                <v-img
                  @click="openImageSelect"
                  round
                  contain
                  :src="
                    item.image == ''
                      ? require('../../assets/image_icon.png')
                      : serverURI + 'public/' + scope + '/' + item.user.avatar
                  "
                  width="150"
                  height="150"
                ></v-img>
                <p>الصورة الشخصية</p>
              </v-card>
            </v-col>
            <v-col cols="4">
              <v-card style="cursor: pointer" width="150">
                <v-img
                  @click="openImageSelect"
                  round
                  contain
                  :src="
                    item.image == ''
                      ? require('../../assets/image_icon.png')
                      : serverURI + 'public/' + scope + '/' + item.user.avatar
                  "
                  width="150"
                  height="150"
                ></v-img>
                <p>صورة فحص كورونا</p>
              </v-card>
            </v-col>
            <v-col cols="4">
              <v-card style="cursor: pointer" width="150">
                <v-img
                  @click="openImageSelect"
                  round
                  contain
                  :src="
                    item.image == ''
                      ? require('../../assets/image_icon.png')
                      : serverURI + 'public/' + scope + '/' + item.user.avatar
                  "
                  width="150"
                  height="150"
                ></v-img>
                <p>صورة الهوية الشخصية</p>
              </v-card>
            </v-col>
          </v-row>
          <input
            v-if="inputType == 'edit'"
            type="file"
            class="hideImageInput"
            ref="imageInput"
            @change="selectFile"
            prepend-icon="mdi-camera"
            label="صورة جديدة"
          />

          <v-text-field
            label="أسم العامل"
            v-model="item.user.name"
            dense
            outlined
          ></v-text-field>
          <v-text-field
            label="رقم الهاتف"
            v-model="item.user.phone"
            dense
            outlined
          ></v-text-field>
          <v-text-field
            label="رمز المرور"
            v-model="item.user.password"
            dense
            type="password"
            outlined
          ></v-text-field>
          <v-select
            label="التصنيف"
            v-model="item.categoryID"
            :items="categories"
            item-text="title"
            item-value="ID"
            dense
            outlined
          ></v-select>
          <v-select
            label="الدولة"
            v-model="item.userInfo.countryID"
            :items="countries"
            item-text="title"
            @change="onChangeCountry"
            item-value="ID"
            dense
            outlined
          ></v-select>
          <v-select
            label="المدينة"
            v-model="item.userInfo.cityID"
            :items="cites"
            item-text="title"
            item-value="ID"
            dense
            outlined
          ></v-select>
        </v-container>
        <v-card-actions>
          <v-row>
            <v-col cols="6"
              ><v-btn
                color="success"
                block
                @click="inputType == 'new' ? store() : update()"
                >حفظ</v-btn
              ></v-col
            >
            <v-col cols="6"
              ><v-btn color="info" block @click="itemDialog = false"
                >الغاء</v-btn
              ></v-col
            >
          </v-row>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-data-table
      disable-pagination
      hide-default-footer
      :items="items"
      :headers="headers"
      dense
    >
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title>كافة العمال</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <div>
            <v-text-field
              prepend-inner-icon="mdi-magnify"
              label="بحث"
              v-model="search"
            ></v-text-field>
          </div>
          <v-spacer></v-spacer>
          <v-btn color="primary" dark @click="openNew">
            {{ newItemTitle }}
          </v-btn>
        </v-toolbar>
      </template>
      <template v-slot:[`item.edit`]="{ item }">
        <v-icon color="blue" class="mr-2" @click="showSupplier(item)">
          mdi-eye
        </v-icon>
      </template>
      <template v-slot:[`item.status`]="{ item }">
        <span v-if="item.status == 3" style="color: red"> Blocked </span>
        <span v-if="item.status == 2" style="color: green"> Actuve </span>
        <span v-if="item.status == 1" style="color: blue"> On Hold </span>
      </template>
    </v-data-table>
  </v-container>
</template>
<script>
import axios from "axios";
import { env, Headers } from "../../constants";
export default {
  data() {
    return {
      newItemTitle: "اضافة عامل جديد",
      item: {
        user: {
          name: "",
          phone: "",
          password: "",
        },
        serviceID: 0,
        categoryID: 0,
        supplierImages: [],
        userInfo: {
          countryID: 0,
          cityID: 0,
        },
      },
      items: [],
      itemDialog: false,
      scope: "users",
      headers: Headers.allSuppliers,
      search: "",
      inputType: "new",
      serverURI: env.server,
      image: "",
      load: false,
      categories: [],
      countries: [],
      cites: [],
    };
  },
  methods: {
    install() {
      let vm = this;
      axios
        .get(env.server + "suppliers/indexAllSuppliers")
        .then((res) => {
          vm.items = res.data;
        })
        .catch((err) => {
          console.log(err);
        });

      axios
        .get(env.server + "categories/index")
        .then((res) => {
          vm.categories = res.data;
        })
        .catch((err) => {
          console.log(err);
        });

      axios
        .get(env.server + "countries/index")
        .then((res) => {
          vm.countries = res.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    openOrder(order) {
      this.$router.push({ name: "ShowOrder", params: { id: order.ID } });
    },
    openNew() {
      this.item = {
        user: {
          name: "",
          phone: "",
          password: "",
        },
        serviceID: 0,
        categoryID: 0,
        supplierImages: [],
        userInfo: {
          countryID: 0,
          cityID: 0,
        },
      };
      this.inputType = "new";
      this.itemDialog = true;
    },
    openEdit(item) {
      let vm = this;
      vm.item = item;
      vm.inputType = "edit";
      vm.itemDialog = true;
    },
    openDestroy(item) {
      let vm = this;
      vm.item = item;
      confirm("هل تريد الحذف ؟") && vm.destroy();
    },
    store() {
      let vm = this;

      // ---- Marshel Fixed ---- //
      vm.item.user.status = 2;
      vm.item.user.user_type = 2;

      // ---- End Marshel Fixed ---- //
      axios
        .post(env.server + "suppliers/storeSupplierUser", vm.item)
        .then(() => {
          window.location.reload();
        })
        .catch((err) => {
          console.log(err);
        });
    },
    showSupplier(item) {
      this.$router.push({ name: "ShowSupplier", params: { id: item.ID } });
    },
    openImageSelect() {
      let vm = this;
      vm.$refs.imageInput.click();
    },
    selectFile(event) {
      let vm = this;
      vm.load = true;
      const data = new FormData();
      data.append("image", event.target.files[0]);
      axios
        .post(env.server + "basics/upload_image/" + vm.scope, data)
        .then((res) => {
          vm.load = false;
          vm.item.user.avatar = res.data.image;
          vm.image = vm.serverURI + "public/" + vm.scope + "/" + res.data.image;
        })
        .catch((err) => {
          console.log(err);
          vm.load = false;
          vm.$store.commit("StoreToast", {
            text: "يوجد خطا غير معروف الرجاء المحاولة مرة أخرى",
            color: "red",
          });
        });
    },
    onChangeCountry() {
      let vm = this;
      axios
        .get(
          env.server +
            "cites/indexCitesWithCountryID/" +
            vm.item.userInfo.countryID
        )
        .then((res) => {
          vm.cites = res.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  mounted() {
    this.install();
  },
};
</script>
