<template>
  <div class="ActiveDrivers">
    <v-data-table :items="items" :headers="headers" dense :items-per-page="15">
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title>كافة العمال</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <v-text-field
            prepend-inner-icon="mdi-magnify"
            label="بحث"
            v-model="search"
          ></v-text-field>
        </v-toolbar>
      </template>
      <template v-slot:[`item.driverStatus`]="{ item }">
        <span v-if="item.driverDetails.status == 0" style="color: red">
          غير متوفر
        </span>
        <span v-if="item.driverDetails.status == 1" style="color: red">
          غير متوفر
        </span>
        <span v-if="item.driverDetails.status == 2" style="color: green">
          متوفر
        </span>
        <span v-if="item.driverDetails.status == 3" style="color: blue">
          داخل رحلة
        </span>
      </template>
      <template v-slot:[`item.car`]="{ item }">
        {{
          item.driverCar.carBrand +
          " " +
          item.driverCar.carModel +
          " " +
          item.driverCar.carYear
        }}
      </template>
      <template v-slot:[`item.geo`]="{ item }">
        <maps-button :driverDetail="item.driverDetails" />
      </template>
      <template v-slot:[`item.blockStatus`]="{ item }">
        <block-switch :userID="item.ID" :block="item.block" />
      </template>
      <template v-slot:[`item.show`]="{ item }">
        <v-icon color="blue" @click="showdriver(item)">mdi-eye</v-icon>
      </template>
    </v-data-table>
  </div>
</template>
<script>
import Headers from "../../constants/Headers";
import axios from "axios";
import { env } from "../../constants";
import MapsButton from "./layouts/MapsButton.vue";
import BlockSwitch from "./layouts/BlockSwitch.vue";
export default {
  data() {
    return {
      headers: Headers.activeDrivers,
      items: [],
      search: "",
      indexType: "actives",
    };
  },
  methods: {
    install() {
      let vm = this;
      axios
        .get(env.server + `driversDashboard/indexDrivers/${vm.indexType}`)
        .then((res) => {
          vm.items = res.data;
        })
        .catch((err) => {
          console.log(err);
          alert("There are error");
        });
    },
    showdriver(item) {
      this.$router.push({ name: "ShowDriver", params: { id: item.ID } });
    },
  },
  mounted() {
    this.install();
  },
  components: {
    MapsButton,
    BlockSwitch,
  },
};
</script>
