<template>
  <div class="AddDialog">
    <v-form
      v-model="vform"
      ref="form"
      @submit.prevent="inputType == 'new' ? store() : update()"
    >
      <v-card>
        <v-toolbar dense flat color="primary" dark>
          <v-toolbar-title>اضافة موظف جديد</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <v-spacer></v-spacer>
          <v-btn icon dark @click="closePanel">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="4">
                <v-text-field
                  :rules="rules.required"
                  label="أسم الموظف"
                  dense
                  v-model="user.name"
                  prepend-icon="mdi-account"
                  outlined
                ></v-text-field>
              </v-col>
              <v-col cols="4">
                <v-text-field
                  label="رقم الهاتف"
                  :rules="rules.required"
                  v-model="user.phone"
                  dense
                  prepend-icon="mdi-cellphone"
                  outlined
                ></v-text-field>
              </v-col>
              <v-col cols="4">
                <v-text-field
                  v-if="inputType == 'new'"
                  :rules="rules.required"
                  label="رمز المرور"
                  v-model="user.password"
                  prepend-icon="mdi-key"
                  outlined
                  dense
                  type="password"
                ></v-text-field>
                <v-text-field
                  v-if="inputType == 'edit'"
                  prepend-icon="mdi-key"
                  label="رمز المرور"
                  dense
                  v-model="user.password"
                  outlined
                  type="password"
                ></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="6">
                <v-select
                  label="المسمى الوظيفي"
                  :rules="rules.required"
                  dense
                  :items="roles"
                  outlined
                  item-text="title"
                  prepend-icon="mdi-account-multiple-outline"
                  item-value="ID"
                  v-model="user.roles_id"
                ></v-select>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="closePanel"> الغاء </v-btn>
          <v-btn color="blue darken-1" text type="submit"> حفظ </v-btn>
        </v-card-actions>
      </v-card>
    </v-form>
  </div>
</template>
<script>
import axios from "axios";
import { env } from "../../../constants";
import Headers from "./Headers";
export default {
  props: ["roles", "inputType", "edit"],
  data() {
    return {
      user: Headers.userForm,
      rules: {
        required: [(v) => !!v || "الحقل مطلوب"],
      },
      vform: false,
    };
  },
  methods: {
    store() {
      let vm = this;
      vm.$refs.form.validate();
      if (vm.vform) {
        vm.user.employee.salary = parseFloat(vm.user.employee.salary);
        let _data = {
          user: vm.user,
          employee: vm.user.employee,
        };
        axios
          .post(env.server + "users/employee/store", _data)
          .then((res) => {
            vm.$emit("endNewValue", res.data.user);
            vm.$store.commit("StoreToast", {
              text: "تمت اضافة الموظف بنجاح",
              color: "success",
            });
          })
          .catch((err) => {
            console.log(err);
            if (
              err.response.data.error ==
              "Error 1062: Duplicate entry '0788' for key 'phone'"
            ) {
              vm.$store.commit("StoreToast", {
                text: "يوجد رقم هاتف مشابه لهذا الرقم ",
                color: "red",
              });
            } else {
              vm.$store.commit("StoreToast", {
                text: "يوجد خطا الرجاء المحاولة مرة أخرى",
                color: "red",
              });
            }
          });
      }
    },
    update() {
      let vm = this;
      vm.$refs.form.validate();
      if (vm.vform) {
        let _data = {
          user: vm.user,
          employee: vm.user.employee,
        };
        axios
          .post(env.server + "users/employee/update", _data)
          .then((res) => {
            vm.$emit("endUpdateValue", res.data.users);
            vm.$store.commit("StoreToast", {
              text: "تم التعديل بنجاح",
              color: "info",
            });
          })
          .catch((err) => {
            console.log(err);
            vm.$store.commit("StoreToast", {
              text: "يوجد خطا غير معروف الرجاء المحاولة مرة أخرى",
              color: "red",
            });
          });
      }
    },
    closePanel() {
      this.$emit("closePanel");
    },
  },
  mounted() {
    let vm = this;
    if (vm.inputType == "edit") {
      vm.user = vm.edit;
    } else {
      if (vm.$refs.form) {
        vm.$refs.form.resetValidation();
      }
      vm.user = Headers.userForm;
    }
  },
};
</script>
