<template>
  <div class="UsersManger">
    <v-dialog v-model="dialog" max-width="600px">
      <add-dialog
        v-if="dialog"
        :roles="roles"
        @closePanel="dialog = false"
        :edit="edit"
        :inputType="inputType"
        @endNewValue="endNewValue"
        @endUpdateValue="endUpdateValue"
      />
    </v-dialog>
    <v-container fluid>
      <v-data-table
        :items-per-page="15"
        :search="search"
        :headers="headers"
        class="elevation-1"
        :items="users"
      >
        <template v-slot:top>
          <v-toolbar flat>
            <v-toolbar-title>موظفين ومندوبين</v-toolbar-title>
            <v-divider class="mx-4" inset vertical></v-divider>
            <div>
              <v-text-field
                prepend-inner-icon="mdi-magnify"
                label="بحث"
                v-model="search"
              ></v-text-field>
            </div>
            <v-spacer></v-spacer>
            <v-btn color="primary" dark @click="openNew">
              اضافة موظف جديد
            </v-btn>
          </v-toolbar>
        </template>
        <template v-slot:[`item.edit`]="{ item }">
          <v-icon small color="blue" class="mr-2" @click="openEdit(item)">
            mdi-pencil
          </v-icon>
          <v-icon @click="openRemove(item)" small color="red">
            mdi-delete
          </v-icon>
        </template>
      </v-data-table>
    </v-container>
    <v-dialog v-model="removeDialog" persistent max-width="320px">
      <v-card>
        <v-card-title></v-card-title>
        <v-card-text>سيتم الحذف هل تريد المتابعه ؟</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="removeDialog = false"
            >الغاء</v-btn
          >
          <v-btn color="blue darken-1" text @click="remove"> حذف</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import axios from "axios";
import { env } from "../../../constants";
import Headers from "./Headers";
import AddDialog from "./AddDialog.vue";
export default {
  data() {
    return {
      dialog: false,
      roles: [],
      inputType: "new",
      users: [],
      headers: Headers.userHeaders,
      search: "",
      removeDialog: false,
      selectedRemoveItem: {},
      edit: {},
    };
  },
  methods: {
    installRoles() {
      let vm = this;
      axios.get(env.server + "users/roles/index").then((res) => {
        vm.roles = res.data.roles;
      });
    },
    installUsers() {
      let vm = this;
      axios.get(env.server + "users/employee/index").then((res) => {
        vm.users = res.data.users;
      });
    },
    openEdit(item) {
      let vm = this;
      vm.edit = { ...item, password: "" };
      vm.inputType = "edit";
      vm.dialog = true;
    },
    openNew() {
      this.edit = Headers.userForm;
      this.inputType = "new";
      this.dialog = true;
    },
    endNewValue(user) {
      let vm = this;
      vm.users.push(user);
      vm.dialog = false;
    },
    endUpdateValue(users) {
      let vm = this;
      vm.users = users;
      vm.dialog = false;
    },
    remove() {
      let vm = this;
      axios
        .get(env.server + "users/employee/delete/" + vm.selectedRemoveItem.ID)
        .then((res) => {
          vm.users = res.data.users;
          vm.$store.commit("StoreToast", {
            text: "تم الحذف بنجاح",
            color: "success",
          });
          vm.removeDialog = false;
        });
    },
    openRemove(item) {
      this.selectedRemoveItem = item;
      this.removeDialog = true;
    },
    inputEventsCheck() {
      let vm = this;
      window.addEventListener("keydown", (e) => {
        if (e.key == "F1") {
          vm.dialog = true;
        } else if (e.key == "esc") {
          vm.dialog = false;
        }
      });
    },
    destroyInputsEventsCheck() {
      window.removeEventListener("keydown", () => {});
    },
  },
  mounted() {
    let vm = this;
    vm.installRoles();
    vm.installUsers();
    vm.inputEventsCheck();
  },
  beforeDestroy() {
    this.destroyInputsEventsCheck();
  },
  components: {
    AddDialog,
  },
};
</script>
