<template>
  <div class="RolesManger">
    <v-dialog v-model="dialog" max-width="600px">
      <v-form
        v-model="vform"
        ref="form"
        @submit.prevent="inputType == 'new' ? store() : update()"
      >
        <v-card>
          <v-toolbar flat color="primary" dark>
            <v-toolbar-title>اضافة مسمى وظيفي جديد</v-toolbar-title>
            <v-divider class="mx-4" inset vertical></v-divider>
            <v-spacer></v-spacer>
            <v-btn icon dark @click="dialog = false">
              <v-icon>mdi-close</v-icon>
            </v-btn>
          </v-toolbar>

          <v-card-text>
            <v-container>
              <v-text-field
                outlined
                autofocus
                :rules="rules.required"
                label="العنوان"
                v-model="role.title"
                prepend-icon="mdi-format-title"
              ></v-text-field>
              <v-textarea
                outlined
                label="وصف"
                v-model="role.description"
                rows="3"
                prepend-icon="mdi-card-bulleted-outline"
              ></v-textarea>
              <v-row>
                <v-col cols="6" v-for="i in pages" :key="i.scope">
                  <v-checkbox :label="i.title" v-model="i.active"></v-checkbox>
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="dialog = false">
              الغاء
            </v-btn>
            <v-btn color="blue darken-1" text type="submit"> حفظ </v-btn>
          </v-card-actions>
        </v-card>
      </v-form>
    </v-dialog>
    <v-container class="mt-5" fluid>
      <v-data-table
        disable-pagination
        hide-default-footer
        class="elevation-1"
        :items="roles"
        :headers="headers"
        :search="search"
      >
        <template v-slot:top>
          <v-toolbar flat>
            <v-toolbar-title>المسميات الوظيفية</v-toolbar-title>
            <v-divider class="mx-4" inset vertical></v-divider>
            <div>
              <v-text-field
                prepend-inner-icon="mdi-magnify"
                label="بحث"
                v-model="search"
              ></v-text-field>
            </div>
            <v-spacer></v-spacer>
            <v-btn color="primary" dark @click="openNew">
              اضافة مسمى جديد
            </v-btn>
          </v-toolbar>
        </template>
        <template v-slot:[`item.edit`]="{ item }">
          <v-icon small color="blue" class="mr-2" @click="openEdit(item)">
            mdi-pencil
          </v-icon>
          <v-icon @click="openRemove(item)" small color="red">
            mdi-delete
          </v-icon>
        </template>
      </v-data-table>
    </v-container>
    <v-dialog v-model="removeDialog" persistent max-width="320px">
      <v-card>
        <v-card-title></v-card-title>
        <v-card-text>سيتم الحذف هل تريد المتابعه ؟</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="removeDialog = false"
            >الغاء</v-btn
          >
          <v-btn color="blue darken-1" text @click="remove"> حذف</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import axios from "axios";
import { env, Pages } from "../../constants";
export default {
  data() {
    return {
      dialog: false,
      role: {
        title: "",
        scopes: "",
        description: "",
      },
      pages: [],
      roles: [],
      headers: [
        { text: "#", sortable: true, value: "ID" },
        { text: "العنوان", sortable: true, value: "title" },
        { text: "الصلاحيات", sortable: true, value: "pages" },
        { text: "الوصف", sortable: false, value: "description" },
        { text: "تعديل/حذف", sortable: false, value: "edit" },
      ],
      inputType: "new",
      rules: {
        required: [(v) => !!v || "الحقل مطلوب"],
      },
      vform: false,
      search: "",
      removeDialog: false,
      selectedRemoveItem: {},
    };
  },
  methods: {
    store() {
      let vm = this;
      vm.$refs.form.validate();
      if (vm.vform) {
        let scopes = "";
        let titles = "";
        vm.pages.forEach((trg) => {
          if (trg.active) {
            if (scopes == "") {
              scopes = trg.scope;
              titles = trg.title;
            } else {
              scopes = scopes + "," + trg.scope;
              titles = titles + "," + trg.title;
            }
          }
        });
        let data = {
          ...vm.role,
          scopes: scopes,
          pages: titles,
        };
        axios
          .post(env.server + "users/roles/store", data)
          .then((res) => {
            vm.$store.commit("StoreToast", {
              text: "تمت الأضافة بنجاح",
              color: "success",
            });
            vm.roles.push(res.data.role);
            vm.role = {
              title: "",
              scopes: "",
              description: "",
            };
            vm.pages = Pages;
            vm.dialog = false;
          })
          .catch((err) => {
            console.log(err);
            vm.$store.commit("StoreToast", {
              text: "يوجد خطا الرجاء المحاولة مرة أخرى",
              color: "red",
            });
          });
      }
    },
    install() {
      let vm = this;
      axios
        .get(env.server + "users/roles/index")
        .then((res) => {
          vm.roles = res.data.roles;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    openEdit(item) {
      let vm = this;
      vm.pages.forEach((page) => {
        if (item.scopes.includes(page.scope)) {
          page.active = true;
        } else {
          page.active = false;
        }
      });
      vm.role = {
        title: item.title,
        description: item.description,
        ID: item.ID,
      };
      vm.inputType = "edit";
      vm.dialog = true;
    },
    openNew() {
      if (this.$refs.form) {
        this.$refs.form.resetValidation();
      }
      this.pages = [
        ...Pages,
        { title: "سائق", scope: "driver", active: false },
      ];
      this.role = {
        title: "",
        scopes: "",
        description: "",
      };

      this.inputType = "new";
      this.dialog = true;
    },
    update() {
      let vm = this;
      vm.$refs.form.validate();
      if (vm.vform) {
        let scopes = "";
        let titles = "";
        vm.pages.forEach((trg) => {
          if (trg.active) {
            if (scopes == "") {
              scopes = trg.scope;
              titles = trg.title;
            } else {
              scopes = scopes + "," + trg.scope;
              titles = titles + "," + trg.title;
            }
          }
        });
        let data = {
          ...vm.role,
          scopes: scopes,
          pages: titles,
        };
        axios
          .post(env.server + "users/roles/update", data)
          .then((res) => {
            vm.roles = res.data.roles;
            vm.$store.commit("StoreToast", {
              text: "تم التعديل بنجاح",
              color: "success",
            });
            vm.role = {
              title: "",
              scopes: "",
              description: "",
            };
            vm.inputType = "new";
            vm.pages = Pages;
            vm.dialog = false;
          })
          .catch((err) => {
            vm.$store.commit("StoreToast", {
              text: "يوجد خطا الرجاء المحاولة مرة أخرى",
              color: "red",
            });
            console.log(err);
          });
      }
    },
    remove() {
      let vm = this;
      axios
        .get(env.server + "users/roles/delete/" + vm.selectedRemoveItem.ID)
        .then((res) => {
          vm.roles = res.data.roles;
          vm.removeDialog = false;
          vm.$store.commit("StoreToast", {
            text: "تم الحذف بنجاح",
            color: "success",
          });
        })
        .catch((err) => {
          console.log(err);
        });
    },
    openRemove(item) {
      let vm = this;
      vm.selectedRemoveItem = item;
      vm.removeDialog = true;
    },
    inputEventsCheck() {
      let vm = this;
      window.addEventListener("keydown", (e) => {
        if (e.key == "F1") {
          vm.dialog = true;
        } else if (e.key == "esc") {
          vm.dialog = false;
        }
      });
    },
    destroyInputsEventsCheck() {
      window.removeEventListener("keydown", () => {});
    },
    installPages() {
      this.pages = Pages;
    },
  },
  mounted() {
    let vm = this;
    vm.installPages();
    vm.install();
    vm.inputEventsCheck();
  },
  beforeDestroy() {
    this.destroyInputsEventsCheck();
  },
};
</script>
